.menu-top {
    background: #2824a7;
    height: 40px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    width: 100%;
    z-index: 100;
    
    a {
        font-family: 'Hanalei Fill', cursive;
        color: #ffffff;
        font-size: 18px;
        font-weight: 500;
    }
    .active {
        color: #000000;
    }
    .menu-nav {
        display: flex;
        .menu-item {
            padding-right: 25px;
            /* .sm(display, none); */
        }
        .burger {
            display: none;
            /* .sm(display, block);
            font-size: 20px;
            color: #ffffff; */
        }
        .close {
            display: none;
        }
    }
    
}

    
#particles-js {
    width: 100%;
    height: 100vh;
    background: url(../img/7.gif);
}

.bio {
    background: url()no-repeat center center / cover;
    width: 100%;
    .text {
        position: absolute;
        top: 37%;
        left: 7%;
        h3 {
            color: #ffffff;
            text-transform: uppercase;
            font-family: 'Major Mono Display', monospace;
            font-size: 50px;
            .xl(font-size,
            30px);
            .md(display,
            none);
        }
        p {
            color: #ffffff;
            text-transform: uppercase;
            font-family: 'Major Mono Display', monospace;
            font-size: 25px;
            padding-top: 40px;
            padding-left: 150px;
            .xl(padding-bottom,
            10px);
            .xl(padding-left,
            80px);
            .xl(font-size,
            17px);
            .lg(padding-left,
            80px);
            .lg(font-size,
            17px);
            .md(display,
            none);
        }
        span {
            color: #ffffff;
            text-transform: uppercase;
            font-family: 'Major Mono Display', monospace;
            font-size: 30px;
            padding-left: 400px;
            .xl(font-size,
            17px);
            .xl(padding-left,
            150px);
            .md(display,
            none);
        }
    }
}